import React, { useState } from "react";
// import Image from "../../components/Image/Image";
import "./Home.scss";
import { Button, Flex, InputItem, WhiteSpace } from "antd-mobile";
import sample from "lodash/sample";

export default function Home(): React.ReactElement {
  const strNull = "  ";
  const [str, setStr] = useState<string>("");
  const [res, setRes] = useState<string>(strNull);
  function sumbit() {
    const arr = str?.split(" ") || [];
    setRes(sample(arr) || strNull);
  }
  function clear () {
    setRes(strNull);
    setStr("")
  }
  return (
    <div className="container">
      <div>
        <InputItem
          placeholder="用“ ”空格分隔"
          value={str}
          onChange={(e) => {
            setStr(e);
          }}
          clear
        />
        <WhiteSpace />
        <div></div>
        <h1><pre>{`结果：${res}`}</pre></h1>
        <div className="button-group">
          <Flex>
            <Flex.Item>
              <Button icon="check" className="default-button" type="primary" onClick={sumbit} disabled={!str?.length}>
                随机
              </Button>
            </Flex.Item>
            <Flex.Item>
              <Button icon="cross" className="default-button" type="warning" onClick={clear}>
                清空
              </Button>
            </Flex.Item>
          </Flex>
        </div>
      </div>
    </div>
  );
}
